import React, {  useEffect } from "react";
import {  useSelector, useDispatch } from "react-redux";
import $ from "jquery";
import { addCartItem, removeCartItem } from "../action/userAction";

export const DetailHeading = (props) => {
    const course = props.course;
    const userInfo = useSelector((state) => state.userStatus.userInfo);
    const dispatch = useDispatch();
    const addCartHandler = (courseId) => {
        dispatch(addCartItem(courseId));
    };
    const removeCartItemHandler = (id) => {
        dispatch(removeCartItem(id));
    };
    useEffect(() => {
        $(".heart").hover(function () {
            $("#hearts").removeClass("far fa-heart heartss");
            $("#hearts").addClass("fas fa-heart hearted");
        });
        $(".heart").mouseout(function () {
            $("#hearts").removeClass("fas fa-heart heartss");
            $("#hearts").addClass("far fa-heart hearted");
        });
        return () => {
            $(".heart").unbind();
        };
    }, []);
    useEffect(() => {
        $(window).scroll(function () {
            if ($(window).scrollTop() >= $("div.fullwitdh").height() + 100) {
                $("#right").css("position", "fixed");
                $("#right").css("margin-top", "100px");
                $("img.course-image").hide("slow", function () {});
            } else {
                $("#right").css("position", "absolute");
                $("#right").css("margin-top", "0px");
                $("img.course-image").show("slow", function () {});
            }
        });
        return () => {
            $(window).unbind();
        };
    }, []);
    return (
        <div class="row">
            <div class="col-8">
                <div class="left">
                    <h1 class="name-course">{course.title}</h1>
                    <div class="note"></div>
                    <div class="left-row">
                        <div class="rating-star">
                            <span class="fa fa-star checked"></span>
                            <span class="fa fa-star checked"></span>
                            <span class="fa fa-star checked"></span>
                            <span class="fa fa-star"></span>
                            <span class="fa fa-star"></span>
                        </div>
                        <div class="rating-details" style={{ marginTop: 8 }}>
                            <span>{course.rating} (1,111 đánh giá)</span>
                        </div>
                        <div class="student-enroll" style={{ marginTop: 8 }}>
                            <span>Có {course.numOfStudent} học sinh đã đăng ký</span>
                        </div>
                    </div>
                    <div class="left-row">
                        <div class="author">Giảng Viên: {course.lecture}</div>
                    </div>
                    <div class="last-update" style={{ marginTop: 25 }}>
                        <span>Sửa đổi lần cuối: 17/06/2020</span>
                    </div>
                </div>
            </div>
            <div class="col-4">
                <div
                    class="jsright right-col-content"
                    id="right"
                    style={{ width: 354, left: "auto", top: 10, position: "absolute" }}
                >
                    <div class="right-module">
                        <div class="image-course">
                            <img
                                class="course-image"
                                src={`/${course.pic}`}
                                alt=""
                                style={{ width: "100%", height: 140 }}
                            />
                        </div>
                        <div class="right-content">
                            <div class="Detail-floating">
                                <div class="buy">
                                    <div class="price">
                                        <div class="now-price">
                                            <span class="course-price-now">{course.price} VND</span>
                                        </div>
                                        <div class="non-purchase">
                                            <span class="heart">
                                                <i id="hearts" class="far fa-heart heartss"></i>
                                            </span>
                                        </div>
                                    </div>
                                    <div class="confirm-buy">
                                        {userInfo ? (
                                            userInfo.cart.includes(course._id) ? (
                                                <div class="button-add-to-cart" style={{ marginTop: 10 }}>
                                                    <button
                                                        type="button"
                                                        class="btn btn-danger"
                                                        style={{ width: "100%", height: 50, backgroundColor: "#4CAF50" }}
                                                        onClick={() => {
                                                            removeCartItemHandler(course._id);
                                                        }}
                                                    >
                                                        Bỏ ra khỏi giỏ hàng
                                                    </button>
                                                </div>
                                            ) : (
                                                <div class="button-add-to-cart" style={{ marginTop: 10 }}>
                                                    <button
                                                        type="button"
                                                        class="btn btn-danger"
                                                        style={{ width: "100%", height: 50 }}
                                                        onClick={() => {
                                                            addCartHandler(course._id);
                                                        }}
                                                    >
                                                        Thêm vào giỏ hàng
                                                    </button>
                                                </div>
                                            )
                                        ) : (
                                            <div class="button-buy-now" style={{ marginTop: 10 }}>
                                                <button
                                                    type="button"
                                                    class="btn btn-primary"
                                                    style={{ width: "100%", height: 50 }}
                                                >
                                                    Đăng nhập để thêm vào giỏ hàng
                                                </button>
                                            </div>
                                        )}
                                    </div>
                                </div>
                                <div class="includes">
                                    <ul class="list">
                                        <li>
                                            <i class="far fa-clock"></i>
                                            <span>20h học</span>
                                        </li>
                                        <li class="list-item">
                                            <i class="far fa-file"></i>
                                            <span>150 bài viết</span>
                                        </li>
                                        <li class="list-item">
                                            <i class="fas fa-heartbeat"></i>
                                            <span>Truy cập trọn đời</span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
