import React, { useEffect } from "react";
import $ from 'jquery'

export const DetailMain = (props) => {
    const {course} = props
    useEffect(() => {
        $("a.learning-item").click(function() {
            if($(this).find('span.plus:visible').length === 0) {
                $(this).find('span.plus').css('display','inline-block');
                $(this).find('span.minus').css('display','none');
                $(this).next().slideToggle();
               
            } else {
                $(this).find('span.plus').css('display','none');
                $(this).find('span.minus').css('display','inline-block');
                $(this).next().slideToggle();
            }
    });
        return () => {
            //
        }
    }, [course])
     
    const detail2 = course.detail||[]
    return (
        <div>
            <div class="row">
                <div class="col-8">
                    <div class="title">
                        <h1>Nội dung khoá học</h1>
                    </div>
                    <div class="left-main-content">
                        {detail2.map((title) => {
                            return (
                                <div key={title.title}>
                                    <a href="javascript:void(0)" class="learning-item">
                                        <div class="learning-item-left">
                                            <span class="title-wrapper">
                                                <span class="toogle">
                                                    <span class="plus">
                                                        <i class="fas fa-plus"></i>
                                                    </span>
                                                    <span class="minus" style={{ display: "none" }}>
                                                        <i class="fas fa-minus"></i>
                                                    </span>
                                                </span>
                                                <span class="text-left">{title.title}</span>
                                            </span>
                                        </div>
                                        <div class="learning-item-right">
                                            <span class="lectures">{title.chapter.length} Bài giảng</span>
                                            <span class="time-learning">{title.totalTime}</span>
                                        </div>
                                    </a>
                                    <div style={{ display: "none" }}>
                                        {title.chapter.map((chapter) => {
                                            return (
                                                <div key={chapter.name} class="lecture-container collapsing multi-collapse">
                                                    <div class="left-content">
                                                        <span class="play-circle">
                                                            <i class="far fa-play-circle"></i>
                                                        </span>
                                                        <div class="title-top">
                                                            <span class="title">
                                                                <a href="">{chapter.name}</a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                    <div class="right-content-preview-time">
                                                        <span>{chapter.time}</span>
                                                    </div>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </div>
    );
};
